import { Row, Col } from "react-bootstrap";
import BottleNumbersInBatch from "../batchdetailspage/components/bottleNumbersInBatch";
import COLLarge from "../batchdetailspage/components/ColLarge";
import COLSmall from "../batchdetailspage/components/ContRowCol";
import COLSmallIngredient from "../batchdetailspage/components/COLSmallIngredient";

export default function PartnerSection(props) {
  return (
    <>
      {" "}
      <Row className="justify-content-md-center">
        <Col
          className="subtitlePadding"
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <h1 style={{ color: "white" }}>Partner Farms</h1>
        </Col>

        {Object.keys(props.ingredients).map((ing) => (
          <COLSmallIngredient
            id="lavenderCard"
            spac="2"
            title={props.ingredients[ing].plant}
            origin={props.ingredients[ing].origin}
            component={
              <>
                <p>{props.ingredients[ing].description}</p>
              </>
            }
          />
        ))}
      </Row>
    </>
  );
}
