import { Row, Col } from "react-bootstrap";
import BottleNumbersInBatch from "../batchdetailspage/components/bottleNumbersInBatch";
import COLLarge from "../batchdetailspage/components/ColLarge";
import COLSmall from "../batchdetailspage/components/ContRowCol";

export default function BatchSection(props) {
  return (
    <>
      <Row className="justify-content-md-center">
        <Col
          className="subtitlePadding"
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <h1 style={{ color: "white" }}>Batch</h1>
        </Col>{" "}
        <COLSmall
          id="woodyCard"
          spac="2"
          title="Verification"
          component={
            <>
              <div style={{ display: "inline-block" }}>
                <p>Verified</p>
              </div>
            </>
          }
        />
        <COLSmall
          id="woodyCard"
          spac="2"
          title="Your Bottle is"
          component={
            <p>
              {props.bottleNumber}/{props.bottleNumbers.length}
            </p>
          }
        />
        <COLLarge
          title="Bottles in Batch"
          component={
            <BottleNumbersInBatch bottleNumbers={props.bottleNumbers} />
          }
        />
      </Row>
    </>
  );
}
