import { Row, Col } from "react-bootstrap";
import BottleNumbersInBatch from "../batchdetailspage/components/bottleNumbersInBatch";
import COLLarge from "../batchdetailspage/components/ColLarge";
import COLSmall from "../batchdetailspage/components/ContRowCol";
import COLXL from "../batchdetailspage/components/ColXL";
import SpecialistVarieties from "../batchdetailspage/components/SpecialistVarieties";

export default function OilSection(props) {
  return (
    <>
      {" "}
      <Row className="justify-content-md-center">
        <Col
          className="subtitlePadding"
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <h1 style={{ color: "white" }}>Oils</h1>
        </Col>
        <COLSmall
          id="lavenderCard"
          spac="2"
          title="Specialist Accord"
          component={<p>{props.keyCrop}</p>}
        />
        <COLSmall
          id="woodyCard"
          spac="2"
          title="Harvested & Distilled on"
          component={<p>{props.dateOfHarvesting}</p>}
        />
        <COLSmall
          id="woodyCard"
          spac="2"
          title="Harvested by"
          component={<p>{props.harvester}</p>}
        />
        <COLSmall
          id="woodyCard"
          spac="2"
          title="Grown at"
          component={<p>Scottish Lavender Oils, Tarhill Farm, Kinross</p>}
        />
        <COLSmall
          id="woodyCard"
          spac="2"
          title={<p>{props.dateOfHarvesting}</p>}
          component={
            <>
              <img width="100%" src={props.imageHarvesting} />
            </>
          }
        />

        <COLSmall
          id="woodyCard"
          spac="2"
          title={<p>{props.dateOfHarvesting}</p>}
          component={<img width="100%" alt="" src={props.imageDistilling} />}
        />

        <COLXL
          title="Ingredients grown on our farm"
          component={
            <SpecialistVarieties varieties={props.specialistVarieties} />
          }
        />
      </Row>
    </>
  );
}
