import "bootstrap/dist/css/bootstrap.min.css";

import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import BatchSection from "../../pageComponents/batch";
import PerfumeSection from "../../pageComponents/perfume";
import ProfileSection from "../../pageComponents/profile";
import OilSection from "../../pageComponents/oils";
import PartnerSection from "../../pageComponents/partner";

export default function RouterFunction(props) {
  return (
    <Routes>
      <Route
        path="/batch"
        element={
          <BatchSection
            bottleNumber={props.bottleNumber}
            bottleNumbers={props.bottleNumbers}
          />
        }
      />
      <Route
        path="/perfume"
        element={
          <PerfumeSection
            perfumeImage={props.perfumeImage}
            nameOfPerfume={props.nameOfPerfume}
            dateOfCompounding={props.dateOfCompounding}
            dateOfBottling={props.dateOfBottling}
            description={props.description}
            imageCompounding={props.imageCompounding}
            imageBottling={props.imageBottling}
          />
        }
      />
      <Route
        path="/oil"
        element={
          <OilSection
            keyCrop={props.keyCrop}
            dateOfHarvesting={props.dateOfHarvesting}
            harvester={props.harvester}
            imageHarvesting={props.imageHarvesting}
            imageDistilling={props.imageDistilling}
            specialistVarieties={props.specialistVarieties}
          />
        }
      />
      <Route
        path="/partner"
        element={<PartnerSection ingredients={props.ingredients} />}
      />
      <Route
        path="/profile"
        element={
          <ProfileSection
            notesData={props.notesData}
            perfumeDescription={props.perfumeDescription}
          />
        }
      />
    </Routes>
  );
}
