import { Row, Col } from "react-bootstrap";
import BottleNumbersInBatch from "../batchdetailspage/components/bottleNumbersInBatch";
import COLLarge from "../batchdetailspage/components/ColLarge";
import COLSmall from "../batchdetailspage/components/ContRowCol";
import Notes from "../batchdetailspage/components/Notes";
import ScentChart from "../batchdetailspage/components/scentChart";
import PerfumeDescription from "../batchdetailspage/components/perfumeDescription";

export default function ProfileSection(props) {
  return (
    <>
      <Row className="justify-content-md-center">
        <Col
          className="subtitlePadding"
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <h1 style={{ color: "white" }}>Perfume Profile</h1>
        </Col>

        <COLLarge
          title="Marina's View from Italy"
          component={
            <PerfumeDescription perfumeDescription={props.perfumeDescription} />
          }
        />
        <COLLarge
          title="Marina"
          component={
            <video
              controls
              width="100%"
              src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/output_compress-video-online.com_bedf781f-9ec4-4a96-a808-10fa0c84a111.mp4?v=1643733267"
            />
          }
        />

        <COLLarge component={<ScentChart />} />
        <COLLarge
          title="Top Notes"
          component={<Notes notes={props.notesData.top} />}
        />
        <COLLarge
          title="Heart Notes"
          component={<Notes notes={props.notesData.heart} />}
        />
        <COLLarge
          title="Base Notes"
          component={<Notes notes={props.notesData.bottom} />}
        />
        <COLLarge
          title="Note"
          component={
            <p>
              The scents reflected above do not necessarily correspond to the
              ingredients used. Different ingredients can change scent when
              mixed.
            </p>
          }
        />

        <COLLarge
          title="Perfume Video"
          component={
            <video
              controls
              width="100%"
              src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/output_compress-video-online.com_bedf781f-9ec4-4a96-a808-10fa0c84a111.mp4?v=1643733267"
            />
          }
        />
      </Row>
    </>
  );
}
